.snippetTitle {
  text-align: center;
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black; /* If you want dots under the hoverable text */
}

/* Tooltip text */
.snippetTitle .snippetTooltip {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  padding: 5px 0;
  border-radius: 6px;

  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
  display: flex;
  flex: 1;
  flex-wrap: nowrap;
  bottom: 100%;
}

/* Show the tooltip text when you mouse over the tooltip container */
.snippetTitle:hover .snippetTooltip {
  visibility: visible;
}
